@use '../base/mixins' as m;
@use '../base/variables' as v; 

.escuela {
    @include m.bg-image('../img/chef_bg'); 
    padding: 20rem 0;
    text-align: center;
    color: v.$blanco;

    &__heading {
        font-weight: bold;
    }

    &__texto {
        font-size: 2.6rem;
        max-width: 50rem;
        margin: 0 auto;
    }

    &__enlace {
        @include m.boton(v.$primario, v.$blanco); 
    }
}